<template>
<div class="signup-result">
  <div>
    <h1>{{status}}</h1>
    <p v-if="registerMember">{{registerMember.name}}</p>
    <p v-if="err">{{err}} </p>
    <p>10 </p>
  </div>  
</div>
</template>
<script>
import {store} from '../store.js'

export default{
    data(){
        return{
            status:'',
            registerMember:'',
            err:''
        }
    },
    created(){
        this.status = store.status.registorStatus.status;
        if(this.status === ''){
            this.err= store.status.registorStatus.err;
        }else if(this.status === ''){
            this.registerMember = store.status.registorStatus.registerMember;
        }
    },
    mounted(){
        setTimeout(() => {this.$router.push({path:'/'})},1000 * 10);
    }
}
</script>

<style>
.signup-result{
    height:200px;
    display:flex;
    align-items:center;
}
.signup-result div{
    width:300px;
    margin:auto;
    border:1px solid red;
}
</style>